import './FabricanteVenta.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import Select from 'react-select'

import { Page } from '../../components'
import { FORMATO_FECHA_BACKEND, RUTAS_APP_FABRICANTE } from '../../constants'
import { useStores } from '../../models/root-store/root-store-context'
import { Producto, ProductoPedido } from '../../services/api'
import { DATE_FORMAT } from '../../utils/date'
import { ResponsivePie } from '@nivo/pie'
import { BasicTooltip } from '@nivo/tooltip'
import { ResponsiveBar } from '@nivo/bar'
import { ICONOS } from '../../helpers-ts/iconos'

const numeral = require('numeral')
const moment = require('moment-timezone')

export const FabricanteVenta: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Pedidos, setPedidos] = useState([] as any) // Mismo que ProductoPedido pero con datos del pedido
  const [Loading, setLoading] = useState(true)

  const [MasVendido, setMasVendido] = useState<Producto>()
  const [MasVisto, setMasVisto] = useState<Producto>()
  const [Favorito, setFavorito] = useState<Producto>()
  const [MenosVendido, setMenosVendido] = useState<Producto>()

  // graficos
  const [ProductosGraficoProcentaje, setProductosGraficoProcentaje] = useState<any[]>([])
  const [ProductosGraficoBarra, setProductosGraficoBarra] = useState<any[]>([])
  const [ProductosGrafico, setProductosGrafico] = useState<any[]>([])

  const [Filtros, setFiltros] = useState({
    fecha_seleccionada: moment().format(),
  } as any)

  const [OrdenarPor, setOrdenarPor] = useState<'fecha' | 'nombre' | 'cantidad' | 'precio' | null>()
  const [OrdenDescendente, setOrdenDesendente] = useState<boolean>(true)
  const [PedidosOrdenado, setPedidosOrdenado] = useState([] as any)

  const [ModalFiltros, setModalFiltros] = useState(false)
  const [ModalOrden, setModalOrden] = useState(false)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const [Paginacion, setPaginacion] = useState({ pagina: 1, paginas: 1, pageSize: 10 })

  const estados = [
    { value: 10, label: 'Confirmado' },
    { value: 20, label: 'Finalizado' },
  ]

  const ordenOptions = [
    { value: 'fecha', label: 'Fecha' },
    { value: 'nombre', label: 'Nombre' },
    { value: 'cantidad', label: 'Cantidad' },
    { value: 'precio', label: 'Precio' },
  ]
  const tipoOrden = [
    { value: true, label: 'Descendente' },
    { value: false, label: 'Ascendente' },
  ]
  const setFiltro = (clave: string, valor: any) => {
    const filtros = { ...Filtros }
    filtros[clave] = valor
    setFiltros(filtros)
  }

  const setPagina = (pagina: number) => {
    const paginacion = { ...Paginacion }
    paginacion.pagina = pagina
    setPaginacion(paginacion)
    obtenerVentas(false, paginacion)
  }

  const PieTooltip = function PieTooltip(_ref: any) {
    var datum = _ref.datum
    return React.createElement(BasicTooltip, {
      id: datum.label,
      value: datum.formattedValue,
      enableChip: true,
      color: datum.color,
    })
  }

  React.useEffect(() => {
    const filtros = { ...Filtros }
    const seleccionada = Filtros.fecha_seleccionada
    filtros.fecha_inicio = moment(seleccionada).startOf('month').subtract(1, 'days').format(FORMATO_FECHA_BACKEND)
    filtros.fecha_fin = moment(seleccionada).endOf('month').add(1, 'days').format(FORMATO_FECHA_BACKEND)
    setFiltros(filtros)
    //
    obtenerVentas()
    obtenerDestacados()
  }, [Filtros.fecha_seleccionada])

  React.useEffect(() => {
    const location = history.location as any
    if (location.state !== undefined && history.location.state !== null && location.state.hasOwnProperty('tipo')) {
      // @ts-ignore
      if (history.location.state.tipo === 'pendiente') {
        setFiltro('estado', 0)
        // @ts-ignore
      } else if (history.location.state.tipo === 'retirar') {
        setFiltro('estado', 10)
      } else {
        setFiltro('estado', 20)
      }
    }
  }, [])

  const obtenerVentas = (reiniciarPagina = false, paginacion: any = null) => {
    setLoading(true)
    //
    paginacion = paginacion || { ...Paginacion }
    paginacion.pagina = reiniciarPagina ? 1 : paginacion.pagina
    //
    const filtros = { ...Filtros, ordenar_por: OrdenarPor, orden_descendente: OrdenDescendente }
    filtros.page = paginacion.pagina
    //
    rootStore.environment.api.adminProductosVentasListado(filtros).then((result) => {
      if (result.kind === 'ok') {
        const data = result.data as any
        setPedidos(data.results)
        //
        paginacion.paginas = Math.ceil(data.count / paginacion.pageSize)
        if (paginacion.pagina > paginacion.paginas) {
          paginacion.pagina = paginacion.paginas
        }
        setPaginacion(paginacion)
      }
      setLoading(false)
    })
  }

  const obtenerDestacados = () => {
    setLoading(true)
    rootStore.environment.api.adminProductosVentasResumen(Filtros.fecha_inicio, Filtros.fecha_fin).then((result) => {
      if (result.kind === 'ok') {
        setMasVendido(result.data.mas_vendido)
        setMasVisto(result.data.mas_visto)
        setFavorito(result.data.favorito)
        setMenosVendido(result.data.menos_vendido)
        setProductosGrafico(result.data.productos_grafico)
        setProductosGraficoProcentaje(result.data.productos_grafico_porcentaje)
        setProductosGraficoBarra(result.data.productos_grafico_barra)
      }
      setLoading(false)
    })
  }
  const MenuOrden = (
    <div className="menu-filtros" style={{ display: ModalOrden ? 'block' : 'none' }}>
      <h4>Orden</h4>
      <Select
        value={ordenOptions.filter((opt) => opt.value === OrdenarPor)}
        onChange={(val) => {
          !!val ? setOrdenarPor(val.value as any) : setOrdenarPor(null)
        }}
        options={ordenOptions}
        className="my-3"
        placeholder={'Ordenar por'}
        isClearable
        theme={(theme) => ({
          ...theme,
          colors: {
            ...theme.colors,
            primary: '#303AB2',
            neutral50: 'black',
          },
        })}
      />
      <Select
        value={OrdenDescendente ? tipoOrden[0] : tipoOrden[1]}
        onChange={(val) => {
          val?.value ? setOrdenDesendente(true) : setOrdenDesendente(false)
        }}
        options={tipoOrden}
        placeholder={''}
        theme={(theme) => ({
          ...theme,
          colors: {
            ...theme.colors,
            primary: '#303AB2',
            neutral50: 'black',
          },
        })}
      />
      <div className="text-right mt-3">
        <button
          className="btn btn-primary"
          onClick={() => {
            setModalOrden(false)
            obtenerVentas(true)
          }}
        >
          Aplicar
        </button>
      </div>
    </div>
  )

  const MenuFiltros = (
    <div className="menu-filtros" style={{ display: ModalFiltros ? 'block' : 'none' }}>
      <h4>Filtros</h4>
      <input
        type="search"
        className="form-control mb-3"
        placeholder={'Nombre del producto'}
        value={Filtros.search}
        onChange={(evt) => setFiltro('search', evt.target.value)}
      />
      <Select
        value={estados.filter((opc: any) => opc.value === Filtros.estado)[0]}
        onChange={(val) => {
          val ? setFiltro('estado', val.value) : setFiltro('estado', null)
        }}
        options={estados}
        placeholder={'Estado'}
        isClearable
        theme={(theme) => ({
          ...theme,
          colors: {
            ...theme.colors,
            primary: '#303AB2',
            neutral50: 'black',
          },
        })}
      />
      <div className="text-right mt-3">
        <button
          className="btn btn-primary"
          onClick={() => {
            setModalFiltros(false)
            obtenerVentas(true)
          }}
        >
          Aplicar
        </button>
      </div>
    </div>
  )

  const renderPaginas = () => {
    const paginas = [] as any
    for (let i = 1; i <= Paginacion.paginas; i++) {
      const clase = i === Paginacion.pagina ? 'actual' : ''
      paginas.push(
        <span key={i} className={`cpointer p-2 ${clase}`} onClick={() => setPagina(i)}>
          {i}
        </span>,
      )
    }
    return <div className="paginacion">{paginas}</div>
  }

  const RenderPedido = (item: any) => {
    return !!item.orden_venta?.fecha ? (
      <tr
        key={item.id}
        className="cpointer"
        onClick={() => history.push(RUTAS_APP_FABRICANTE.VENTA_DETAIL(item.orden_venta.id))}
      >
        <td>{moment(item.orden_venta.fecha).format(DATE_FORMAT.DATE)}</td>
        <td>
          {item.producto.imagenes.length > 0 && (
            <img src={item.producto.imagenes[0].imagen} width={45} height={45} className="mr-2" />
          )}
          {item.producto.nombre}
        </td>
        <td className="text-center" style={{ lineHeight: '45px' }}>
          {numeral(item.cantidad).format('0,0')}
        </td>
        <td className="text-right" style={{ lineHeight: '45px' }}>
          {numeral(item.costo).format('0,0')} Gs.
        </td>
      </tr>
    ) : null
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'ventas-productos-fabricante'}>
      <div className={'mx-3 py-2'}>
        <div className="row align-items-center text-center py-2">
          <div className="col-lg-10 col-md-10 col-12 ">
            <div
              className="d-flex justify-content-between mx-auto"
              id="fecha-slide"
              style={{ color: 'var(--success)' }}
            >
              <a
                onClick={() => {
                  const currentDate = moment(Filtros.fecha_seleccionada)
                  setFiltro('fecha_seleccionada', currentDate.subtract(1, 'months'))
                }}
              >
                <FIcon.ChevronLeft size={40} />
              </a>

              <div>
                <span style={{ fontWeight: 'bolder', textTransform: 'uppercase', fontSize: '1.5rem' }}>
                  {moment(Filtros.fecha_seleccionada).startOf('month').format('MMMM')}
                </span>
                <br />
                <p style={{ textAlign: 'center' }}>
                  {moment(Filtros.fecha_seleccionada).startOf('month').format('YYYY')}
                </p>
              </div>
              <a
                onClick={() => {
                  const selectedDate = moment(Filtros.fecha_seleccionada).add(1, 'months')
                  const current = moment()
                  if (current.isAfter(selectedDate)) {
                    setFiltro('fecha_seleccionada', selectedDate)
                  }
                }}
              >
                <FIcon.ChevronRight size={40} />
              </a>
            </div>
          </div>
          <div className="col-md-1 col-lg-1 col-12">
            <div className="w-100 d-flex justify-content-center">
              <a onClick={() => setModalOrden(!ModalOrden)}>
                <span style={{ width: '30px', float: 'right' }}>{ICONOS.tablero}</span>
              </a>
            </div>
            {MenuOrden}
          </div>
        </div>
        {!Loading && (
          <div className="resumen-container">
            <h4 className="text-center mt-lg-0 mt-2 text-lg-left">Resumen de los productos vendidos</h4>

            <div className="row">
              {!!MasVendido && MasVendido.imagenes.length > 0 && (
                <div className={Movil ? 'destacado col-6' : 'destacado col-sm-3'}>
                  <div
                    className="cpointer"
                    onClick={() => history.push(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(MasVendido.id))}
                  >
                    <h5 style={{ color: 'var(--success)' }}> Estrella </h5>
                    <img className="my-2" src={MasVendido.imagenes.length ? MasVendido.imagenes[0].imagen : ''} />
                    <h4 className="nombre"> {MasVendido.nombre}</h4>
                  </div>
                </div>
              )}
              {!!MasVisto && MasVisto.imagenes.length > 0 && (
                <div className={Movil ? 'destacado col-6' : 'destacado col-sm-3'}>
                  <div
                    className="cpointer"
                    onClick={() => history.push(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(MasVisto.id))}
                  >
                    <h5 style={{ color: 'var(--naranja-abi)' }}>Más visto</h5>
                    <img className="my-2" src={MasVisto.imagenes.length ? MasVisto.imagenes[0].imagen : ''} />
                    <h4 className="nombre"> {MasVisto.nombre}</h4>
                  </div>
                </div>
              )}
              {!!Favorito && Favorito.imagenes.length > 0 && (
                <div className={Movil ? 'destacado col-6' : 'destacado col-sm-3'}>
                  <div
                    className="cpointer"
                    onClick={() => history.push(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(Favorito.id))}
                  >
                    <h5 style={{ color: '#fe4b00' }}>Favorito</h5>
                    <img className="my-2" src={Favorito.imagenes.length ? Favorito.imagenes[0].imagen : ''} />
                    <h4 className="nombre"> {Favorito.nombre}</h4>
                  </div>
                </div>
              )}
              {!!MenosVendido && MenosVendido.imagenes.length > 0 && (
                <div className={Movil ? 'destacado col-6' : 'destacado col-sm-3'}>
                  <div
                    className="cpointer"
                    onClick={() => history.push(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(MenosVendido.id))}
                  >
                    <h5 style={{ color: '#e10600' }}>Menor salida</h5>
                    <img className="my-2" src={MenosVendido.imagenes.length ? MenosVendido.imagenes[0].imagen : ''} />
                    <h4 className="nombre"> {MenosVendido.nombre}</h4>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>

      <div className={'row'}>
        <div className={'col-12 col-lg-6'}>
          <h3 className="text-center text-lg-left">Ventas últimos 3 meses</h3>
        </div>
        <div className={'col-12'} style={{ minHeight: 600 }}>
          <ResponsivePie
            data={ProductosGraficoProcentaje}
            margin={{ top: 150, right: 200, bottom: 150, left: 120 }}
            innerRadius={0.5}
            padAngle={0.7}
            cornerRadius={3}
            colors={{ scheme: 'paired' }}
            borderWidth={1}
            borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
            arcLabelsSkipAngle={10}
            arcLabelsTextColor="#333333"
            arcLinkLabelsColor={{ from: 'color' }}
            tooltip={PieTooltip}
          />
        </div>
      </div>

      <div className={'row'}>
        <div className={'col-12 col-lg-6'}>
          <h3 className="text-center text-lg-left">Ventas últimos 6 meses</h3>
        </div>
        <div className={'col-12 text-center text-lg-left'} style={{ minHeight: 600 }}>
          <ResponsiveBar
            data={ProductosGraficoBarra}
            keys={['value']}
            indexBy="mes"
            groupMode="grouped"
            margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
            padding={0.3}
            valueScale={{ type: 'linear' }}
            indexScale={{ type: 'band', round: true }}
            colors={['#303AB2']}
            axisTop={null}
            axisRight={null}
            axisBottom={{
              tickSize: 5,
              tickPadding: 5,
              tickRotation: 0,
              legend: 'Meses (miles de guaraníes)',
              legendPosition: 'middle',
              legendOffset: 32,
            }}
            axisLeft={{
              tickSize: 5,
              tickPadding: 5,
              tickRotation: 0,
              legend: 'Ventas (millones de guaraníes)',
              legendPosition: 'middle',
              legendOffset: -55,
            }}
            labelTextColor="white"
            labelSkipWidth={12}
            labelSkipHeight={12}
            animate={true}
          />
        </div>
      </div>
      <div className={'row'}>
        <div className={'col-12'}>
          <h3 className="text-center text-lg-left">Productos disponibles</h3>
        </div>
        <div className={'col-12'} style={{ minHeight: 600 }}>
          <ResponsivePie
            data={ProductosGrafico}
            margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
            innerRadius={0.5}
            padAngle={0.7}
            cornerRadius={3}
            colors={{ scheme: 'nivo' }}
            borderWidth={1}
            borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
            arcLabelsSkipAngle={10}
            arcLabelsTextColor="#333333"
            tooltip={PieTooltip}
          />
        </div>
      </div>

      <div className="text-center row">
        <div className="titulo col-10 offset-1">Historial de ventas</div>
        <div className="col-1">
          <span
            className="float-right p-3 cpointer boton-filtro"
            title="Filtros"
            onClick={() => setModalFiltros(!ModalFiltros)}
          >
            {/*<FIcon.Sliders size={25} />*/}
            <span style={{ width: '30px' }}>{ICONOS.barraHorizontal}</span>
          </span>
          {MenuFiltros}
        </div>
      </div>

      <div className={Movil ? 'container-lg' : ''}>
        {Loading && <IonSpinner name="crescent" className={'loadingSpinnerFabricanteApp pt-5'} />}
        {!Loading && (
          <>
            <div className="table-responsive" style={{ width: '90%', marginLeft: 'auto', marginRight: 'auto' }}>
              <table className="table table-bordered">
                <thead>
                  <tr style={{ background: '#efefef' }}>
                    <th>Fecha</th>
                    <th>Nombre del producto</th>
                    <th className="text-center">Cantidad</th>
                    <th className="text-center">Precio</th>
                  </tr>
                </thead>
                <tbody>{Pedidos.map(RenderPedido)}</tbody>
              </table>
            </div>
            {renderPaginas()}
          </>
        )}
        {!Loading && Pedidos.length === 0 && (
          <div className={'d-flex py-5'}>
            <span className={'mx-auto my-auto text-gray'}>No se encontraron ventas</span>
          </div>
        )}
      </div>
    </Page>
  )
})

export default FabricanteVenta
